<template>
  <def-layout>

    <div slot="layout_title">新增发票</div>

    <listing-plus-top slot="layout_inner_head" :_listings="listingContent" :_select="select"></listing-plus-top>

    <!-- 表单主体 -->
    <listing-plus-body slot="layout_inner_body"
      :_listingContent="listingContent"
      :_select="select"

      v-if="listingContent.length > 0"
    ></listing-plus-body>

    <transition v-else name="fade" slot="layout_inner_body">
      <loading></loading>
    </transition>

    <!-- 底部插件 -->
    <listing-plus-footer slot="layout_footer" 
      :the_prices="prices" 
      
      @pricesPdfView_Father="pricesPdfView"
      @pricesSubmit_Father="pricesSubmit" 
      ref="lpFooter"
    ></listing-plus-footer>

    <div slot="layout_extra">
      <listing-choise-modal @returnChoised="reciverPrices"></listing-choise-modal>
      <listing-plus-full-msg></listing-plus-full-msg>
      <listing-pdf-modal 
        :membery="prices.membery" 
        :_listing_content="selectContent" 

        :_numed="numed"
        :_timed="timed"
        :_payment="payment"
        v-if="prices != null"
      ></listing-pdf-modal>

      <custom-modal
        :id_prefix="'sureChangePrices'"
        :title="'提示'"
        :txt="'更改所属报价单会造成发票的菜品内容被替换，是否继续？'"
        @done_Father="openPriceChoise"
      >
        <div slot="submit_text">继续</div>
      </custom-modal>
    
      <submit @submitConfirm="submit"></submit>
    </div>
  </def-layout>
</template>

<script>
import $ from 'jquery'
import view from '@/common/es/view'
import tool from '@/common/es/tool'
import validate from '@/common/es/validate'
import config from '@/common/config'

import conn from '@/service/conn'

import DefLayout from '@/components/Layout/DefLayout.vue'
import ListingPlusTop from './Top/ListingPlusTop.vue'
import ListingPlusBody from './Body/ListingPlusBody.vue'
import ListingPlusFooter from './Footer/ListingPlusFooter.vue'

import Loading from '@/components/Shimmer/Loading.vue'
import ListingChoiseModal from '@/components/Modal/Choise/ListingChoiseModal.vue'
import ListingPlusFullMsg from './FullMsg/ListingPlusFullMsg.vue'
import ListingPdfModal from '@/components/Modal/Pdf/ListingPdfModal.vue'
import Submit from '@/components/Modal/Submit.vue'
import CustomModal from '@/components/Modal/CustomModal.vue'

export default {
  name: 'ListingPlus',
  components: {
    DefLayout,
    ListingPlusTop,
    ListingPlusBody,
    ListingPlusFooter,
    Loading,
    ListingChoiseModal,
    ListingPlusFullMsg,
    ListingPdfModal,
    Submit,
    CustomModal
  },
  data() {
    return {
      listingContent: [],
      prices: null,
      select: [],
      selectContent: [],
      isLoading: true,

      timed: '',
      numed: '',
      payment: 0,
      result_price: ''
    }
  },
  mounted() {
    this.loadPrices()

    this.loadContent()
    // this.fullMsg()
  },
  methods: {
    loadPrices() {
      if (this.prices == null) {
        const active_prices = tool.storage._get('listing_active_prices')

        if ((active_prices != null) && (active_prices != '') && (active_prices != undefined)) {
          this.prices = JSON.parse(active_prices)
        } else { this.openPriceChoise() }
        return false
      }
      return true
    },
    loadContent() {
      
      if ( this.prices != null ) {
        const condition = {
          price_collect: this.prices.id
        }
        
        conn.prices_content.getOneByPrices(condition).then(res => {

          if (conn.judge_response_list(res)) {
            
            const _lc = JSON.parse(res[0].content)

            this.listingContent = []
            for (const l in _lc) {
              
              this.listingContent.push({
                ..._lc[l],
                zk: '',
                count: '0.00',
                res: '0.00'
              })
            }
            this.select = [ this.listingContent[0].id ]

          }
          this.isLoading = false
          
        })
      } else {
        tool.showModal('pricesChoiseModal')
      }
    },
    //
    openPriceChoise() {
      tool.showModal('pricesChoiseModal')
    },
    //
    reciverPrices(v) { 
      this.prices = v 
      tool.storage._set('listing_active_prices', JSON.stringify(v))
      this.loadContent()
    },
    collectSelect() {
      this.selectContent = this.listingContent.filter((e) => tool.inList(e.id, this.select))

      this.selectContent = this.selectContent.map((e) => {
        e.res = tool.floatNum.floatMul(e.count, e.price)
        return e
      })

      if (this.select.length > 1) {
        this.result_price = this.selectContent.reduce((pre, current) => tool.floatNum.floatAdd(pre.res, current.res) )
      } else {
        this.result_price = this.selectContent[0].res
      }
    },
    collectMsg() {
      const res = this.$refs.lpFooter.validateInput()
      
      if (res != false) { 
        this.timed = res.timed
        this.numed = res.numed
        this.payment = res.payment
        return true
      }
      return false
    },
    // 提交

    _submit(data) {

      conn.listing.add(data).then(res => {
        
        if (conn.judge_response(res)) {

          conn.listing_content.add({

            listing_id: res.id,
            content: JSON.stringify(this.select_content)
          }).then(res => {

            if (conn.judge_response(res)) {

              tool.showToast('新增成功！！！')
              tool.storage._set('listing_active_prices', '')
              tool.closeAllModal()
              setTimeout(() => {
                this.$router.push('/home/listing')
              }, 700)
            }
          })
        }
      })
    },
    submit() {

      const data = {
        num: this.numed,
        listing_time: this.timed + ' 00:00:00',
        pay_way: this.payment,
        membery_id: this.prices.membery.id,
        price_collect_id: this.prices.id,
        freight_item_num: this.select.length,
        total_price_freight: this.result_price,
        result_price: this.result_price,
      }

      this._submit(data)
    },
    pricesSubmit() {
      const res = this.collectMsg()
      if (res) {
        this.collectSelect()

        const empty = this.selectContent.filter((e) => {
          if (e.res == 0) { return true } return false
        })

        if (empty.length > 0) { 
          tool.showToast(`存在 ${empty.length}项 数量为 0 的选项！！！`) 
        } 
        else { 
          tool.showModal('submitModal')
        }
      }
    },
    pricesPdfView() {
      const res = this.collectMsg()
      if (res) {
        this.collectSelect()
        tool.showModal('listingPdfViewModal')
      }
    },
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/listing.sass"

</style>


