<template>
  <div class="modal fade" tabindex="-1" role="dialog" id="listingPdfViewModal">
    <div class="modal-dialog modal-lg modal-dialog-centered modal-dialog-scrollable">
      <div class="modal-content">
        
        <div class="modal-body">
          <div class="row">
            <div class="col-sm-12 text-center fs-12 mt-2">
              <pdf-header>
                发票
              </pdf-header>
            </div>

            <div class="col-sm-12">
              <table class="pdf-table-outter">
                <tr>
                  <td width="68%">
                    <pdf-invoice-member :the_member="membery"></pdf-invoice-member>
                  </td>
                  <td>
                    <pdf-invoice-msg
                      :listing_num="_numed"
                      :listing_time="_timed"
                      :listing_payment="ser_payment(_payment)"
                      :member_num="membery.num"
                    ></pdf-invoice-msg>
                  </td>
                </tr>
              </table>
            </div>

            <div class="col-sm-12">
              <pdf-invoice-freight :lising_content="_listing_content" v-if="_listing_content != null"></pdf-invoice-freight>
              
            </div>

          </div>
          
          <pdf-footer :member="membery" v-if="membery!=null"></pdf-footer>
        </div>

        <div class="modal-footer fs-12">
          <button class="btn btn-light btn-sm close-btn" data-dismiss="modal">关闭</button>
          <button class="btn btn-outline-info btn-sm print-btn" v-if="isPrint" @click="printPdf">导出 Pdf</button>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
import conn from '@/service/conn'

import service from '@/service'
import tool from '@/common/es/tool'
import view from '@/common/es/view'
import config from '@/common/config'

import PdfFooter from '@/components/Pdf/PdfFooter.vue'
import PdfHeader from '@/components/Pdf/PdfHeader.vue'

import Loading from '@/components/Shimmer/Loading.vue'
import PdfInvoiceMember from '../../Pdf/Invoice/PdfInvoiceMember.vue'
import PdfInvoiceMsg from '../../Pdf/Invoice/PdfInvoiceMsg.vue'
import PdfInvoiceFreight from '../../Pdf/Invoice/PdfInvoiceFreight.vue'

export default {
  components: { 
    PdfFooter,
    PdfHeader,
  
    Loading,
    PdfInvoiceMember,
    PdfInvoiceMsg,
    PdfInvoiceFreight
  },
  name: 'PricesPdf',
  props: {
    _numed: {
      type: String
    },
    _timed: {
      type: String
    },
    _payment: {
      typ: Number
    },
    
    membery: {
      type: Object
    },
    
    isPrint: {
      type: Boolean,
      default: false
    },

    _listing_content: {
      type: Array,
      default: null
    }
  },
  mounted() {
    
  },
  data() {
    return {
      
      isLoading: true
    }
  },
  methods: {
    ser_payment(v) {
      return view.ser_listing_paymet(v)
    },
    printPdf() {
      // const _router = this.$router.resolve({ path: '/pdf_prices', query: { pcc_id: this.pcc_id } })
      // window.open(_router.href, '_blank')
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/pdf.sass"
.close-btn
  padding-left: $horizonXXL
  padding-right: $horizonXXL

.print-btn
  width: 119px
</style>


